<template>
	<view style="width: 100%;background-color: #FFF;height: 100%;">
		<scroll-view scroll-y="true" style="height: 100%;width: 100%;background-color: #FFF;" class="column"
			:refresher-triggered="refresherTriggered" :refresher-enabled="true" :refresher-threshold="45"
			@refresherrefresh="refresherrefresh">
			<view class="headerView">
				<view class="topImageView column">

					<view class="address_2 row" @click="doShowAddress()">
						<image src="../../static/home/home-ads.png" style="width: 14px;height: 14px;margin-left: 15px;"
							mode="aspectFit"></image>
						<view>
							{{curAdsMsg.addr}}
							<!-- {{curAdsMsg.name + ' ' + curAdsMsg.mobile}} -->
						</view>
						<image src="../../static/ads-seld-right.png" style="width: 15px;height: 15px;margin-left: 5px;"
							mode=""></image>
					</view>
					<view class="row topSearchBtn" @click="doSearch('')">
						<image src="../../static/search.png" mode="aspectFit"
							style="margin-left: 10px;width: 20px;height: 20px;"></image>
						<view style="margin-left: 10px;font-size: 14px;color: #6F6F6F;">
							{{i18n.index.search}}
						</view>
					</view>
					<scroll-view scroll-x="true" class="sectionView"
						style="height: 25px;white-space: nowrap;margin-top: 8px;">
						<text v-for="(item,index) in search_keyword" :key="index" class="hotClass"
							@click="doSearch(item.use_name)">{{item.use_name}}</text>
					</scroll-view>
				</view>

				<view class="wihteJianbian">
					<view class="fourthClassView" :style="'margin-top:' + (bannerHeigth - 10) + 'px;'">
						<view v-for="(item,index) in array1" :key="index" class="column"
							style="width: 14.933%;justify-content: center;"
							:style="index == 0 ? 'margin-left: 6.4%' : 'margin-left: 9.06%'"
							@click="array1ItemClick(index,item)">
							<image :src="item.thumb" mode="aspectFit" style="width: 100%;"
								:style="'height:' + fourthImageW + 'px;'"></image>
							<view
								style="font-size: 14px;font-weight: bold;color: #323232;margin-top: 8px;text-align: center;height: 20px;line-height: 20px;">
								{{item.use_name}}
							</view>
						</view>
					</view>

					<view class="row sectionView" style="flex-wrap: wrap;margin-top: 10px;">
						<view v-for="(item,index) in array2" :key="index" class="row array2Item"
							@click="array2ItemClick(index,item)">
							<image :src="item.thumb" mode="" style="width: 100%;height:100%"></image>
							<view class="array2Item-view">
								<view
									style="font-size: 14px;font-weight: bold;color: #383838;margin-left: 15px;flex: 1;"
									:style="'color: ' + item.font_color +';'">
									{{item.use_name}}
								</view>
							</view>


						</view>
					</view>
					<view class="row" style="width: 100%;height: 80px;background-color: #f5f6f7;align-items: center;">
						<view v-for="(item,index) in tabbar" :key="index" class=""
							style="width: 25%;align-items: center;justify-content: center;"
							:style="index != tabbar.length-1 ? 'border-right: #CECECE solid 1px;' : ''"
							@click="tabbarClick(index)">
							<view class="column" style="align-items: center;">
								<view :class="index == tabIndex ? 'tab_sel_title' : 'tab_nor_title'">
									{{item.name}}
								</view>
								<view :class="index == tabIndex ? 'tab_sel_des' : 'tab_nor_des'">
									{{item.des}}
								</view>
							</view>
						</view>
					</view>
				</view>

				<view class="bigImage" :style="'height:' + bannerHeigth + 'px;'" >
					<swiper class="index-banner swiper" circular="false" indicator-dots="true" autoplay="true">
						<swiper-item class="index-banner-img" @click="bannerTo(item)" v-for="(item, index) in banner"
							:key="item.id">
							<image class="index-banner-img" :src="item.image" mode="aspectFill"></image>
						</swiper-item>
					</swiper>
				</view>

			</view>




			<swiper style="width: 100%;background-color: #f5f6f7;" :style="'height:' + scrollHeight + 'px;'"
				@animationfinish="animationfinish" :current="tabIndex">
				<swiper-item>
					<storeList ref="storeList0" :height="scrollHeight" :typeString="0" @gotoCell="gotoCell"></storeList>
				</swiper-item>
				<swiper-item>
					<storeList ref="storeList1" :height="scrollHeight" :typeString="1" @gotoCell="gotoCell"></storeList>
				</swiper-item>
				<swiper-item>
					<storeList ref="storeList2" :height="scrollHeight" :typeString="5" @gotoCell="gotoCell"></storeList>
				</swiper-item>
				<swiper-item>
					<storeList ref="storeList3" :height="scrollHeight" :typeString="3" @gotoCell="gotoCell"></storeList>
				</swiper-item>
			</swiper>
		</scroll-view>
		<botAds :latitude="latitude" :longitude="longitude" :curads="curAdsMsg" v-if="showBotAds && !noLocation"
			@closeAdsView="closeAdsView"></botAds>
		<topAds :latitude="latitude" :longitude="longitude" :curads="curAdsMsg" v-if="showTopAds"
			@closeAdsView="closeAdsView"></topAds>
		<!-- </scroll-view> -->
	</view>
</template>

<script>
	import storeList from '../../components/storeList/index.vue'
	import botAds from "@/components/homeAds/bottom.vue"
	import topAds from "@/components/homeAds/top.vue"

	var amapFile = require('@/libs/amap-wx.130.js'); //如：..­/..­/libs/amap-wx.js
	var myAmapFun = new amapFile.AMapWX({
		key: getApp().globalData.amapKey
	});
	export default {
		components: {
			storeList,
			botAds,
			topAds
		},
		data() {
			return {
				scrollHeight: 0,
				array1: [],
				array2: [],
				tabbar: [],
				tabIndex: 0,
				curAdsMsg: {
					place: '',
					addr: '',
					lat: '',
					lng: ''
				},
				latitude: '0',
				longitude: '0',
				showBotAds: false,
				showTopAds: false,
				noLocation: false,
				refresherTriggered: false,
				banner: [],
				search_keyword: [],
				onhidden: false,
				forgotShowBotAds: false,
				fourthImageW: 0,
				bannerHeigth: 0
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			var that = this
			var res = uni.getSystemInfoSync()
			that.scrollHeight = res.windowHeight - 80 - res.statusBarHeight
			that.fourthImageW = res.windowWidth * 0.14933
			that.bannerHeigth = (res.windowWidth - 30) * 0.38
			that.tabbar = [{
					name: '为你推荐',
					des: '精选店铺'
				},
				{
					name: '吃美食',
					des: '吃遍全城'
				},
				{
					name: '惠购物',
					des: '超市便利'
				},
				{
					name: '找服务',
					des: '优质服务'
				}
			]

		},
		onLoad() {





			var that = this

			// #ifndef H5

			const value = uni.getStorageSync('userinfo');
			if (value && value.id && value.token) {
				console.log(value)
				// uni.hideTabBar()
				// #ifdef APP-PLUS
				that.getclientID()
				// #endif
				that.loginToIM(value)
				
			} else {
				uni.showTabBar()
			}
			that.requestData()
			that.getLocationsss()
			// #endif
			uni.$on('chooseAddressSucess', function(msg) {
				console.log(msg)
				that.closeAdsView(msg)
			})
			
		},
		onUnload() {
			uni.$off('chooseAddressSucess')
		},
		onShow() {
			if (this.noLocation == true) {
				this.getLocationsss()
			}
			if (this.forgotShowBotAds && this.onhidden) {
				this.forgotShowBotAds = false
				this.showBotAds = true
			}
			this.onhidden = false
		},
		onHide() {
			this.onhidden = true
			console.log('onhideindex')
		},
		methods: {
			
			loginToIM(info) {
				var that = this
				var uiddd = info.im.userId;
				const userSig = info.im.UserSig;
				// console.log('uni.$TUIKitEvent.SDK_RELOAD, this.onSDKReload')
				uni.$TUIKit.login({
						userID: uiddd,
						userSig: userSig
					})
					.then(function(imResponse) {
						console.log('uni.$TUIKit.login, sucesss')
						
						
						setTimeout(function() {
							that.uploadIMProfile(info, '')
						}, 2000);
						
						// console.log('imResponse:', imResponse.data); // 登录成功
						if (imResponse.data.repeatLogin === true) {
							// 标识帐号已登录，本次登录操作为重复登录。v2.5.1 起支持
							console.log(imResponse.data.errorInfo);
						}
					})
					.catch(function(imError) {
						console.warn('login error:', imError); // 登录失败的相关信息
						
					});


			},
			uploadIMProfile(info, cid) {
				// 修改个人标配资料
				let promise = uni.$TUIKit.updateMyProfile({
					avatar: info.avatar,
					nick: info.user_nickname,
					selfSignature: cid
				});
				promise.then(function(imResponse) {
					console.log('更新资料成功',imResponse.data); // 更新资料成功
				}).catch(function(imError) {
					console.warn('updateMyProfile error:', imError); // 更新资料失败的相关信息
				});
			},
			getclientID() {
				var that = this
				uni.getPushClientId({
					success: (res) => {
						console.log('111111111', res.cid);
						that.uploadCid(res.cid)
					},
					fail(err) {
						console.log('2222222', err)
					},
					complete: (res) => {
						console.log('111111', res);
					},
				})
			},
			uploadCid(cid){
				var that = this
				that.http.sendRequest('Push.Update', {
					'cid': cid
				}).then(function(res) {})
			},
			showNoLocationAlert() {
				var that = this
				uni.showModal({
					title: that.i18n.kaiqidingweifuwu,
					content: that.i18n.kaiqidingweitishi,
					showCancel: true,
					cancelText: that.i18n.cancel,
					confirmText: that.i18n.qushezhi,
					success: res => {
						uni.showTabBar()
						if (res.confirm) {
							// #ifdef MP-WEIXIN
							wx.openSetting()
							// #endif
							// #ifdef APP-PLUS
							if (getApp().globalData.platform == 'ios') {
								plus.runtime.openURL("app-settings://");
							} else if (getApp().globalData.platform == 'android') {
								var main = plus.android.runtimeMainActivity();
								var Intent = plus.android.importClass("android.content.Intent");
								var mIntent = new Intent('android.settings.WIRELESS_SETTINGS');
								main.startActivity(mIntent);
							}
							// #endif
						}
						// that.showNoLocationAlert()
					},
					fail: () => {
						uni.showTabBar()
					},
					complete: () => {}
				});
			},
			getLocationsss() {

				var that = this
				console.log('getLocationsss')
				myAmapFun.getRegeo({
					success: function(data) {
						that.noLocation = false
						uni.showTabBar()
						that.curAdsMsg.place = data[0].desc
						that.curAdsMsg.addr = data[0].name
						that.curAdsMsg.lat = data[0].latitude
						that.curAdsMsg.lng = data[0].longitude
						that.latitude = data[0].latitude
						that.longitude = data[0].longitude
						uni.setStorageSync('latitude', that.latitude)
						uni.setStorageSync('longitude', that.longitude)
						const value = uni.getStorageSync('userinfo');
						// console.log(value);

						if (value && value.id && value.token) {
							that.checkLocation()
						} else {
							that.closeAdsView(0)
							setTimeout(function() {
								uni.setStorage({
									key: 'userlocation',
									data: that.curAdsMsg,
									success() {

										that.$refs.storeList0.requestData()
										that.$refs.storeList1.requestData()
										that.$refs.storeList2.requestData()
										that.$refs.storeList3.requestData()
									}
								})
							}, 500);
						}

					},
					fail: function(info) {
						that.showNoLocationAlert()
						//失败回调
						console.log('失败回调' + JSON.stringify(info))
						// if (info.errCode == 0) {
						that.noLocation = true
						// uni.showTabBar()
						// that.showNoLocationAlert()
						// }
					}

				})


			},
			checkLocation() {
				console.log('checkLocation')

				var that = this
				that.http.sendRequest('Addr.CheckNewAddress', {
					'lng': that.longitude,
					'lat': that.latitude,
					'place': that.curAdsMsg.place
				}).then(function(res) {
					if (res.code == 0) {
						var a = res.info[0]

						if (a.addr == 0) {
							console.log('1111111111', that.onhidden)
							if (that.onhidden) {
								that.forgotShowBotAds = true
							} else {
								that.forgotShowBotAds = false
								that.showBotAds = true
							}
						} else {
							that.curAdsMsg = a.addr
							that.closeAdsView(0)
							setTimeout(function() {
								uni.setStorage({
									key: 'userlocation',
									data: that.curAdsMsg,
									success() {

										that.$refs.storeList0.requestData()
										that.$refs.storeList1.requestData()
										that.$refs.storeList2.requestData()
										that.$refs.storeList3.requestData()
									}
								})
							}, 500);

						}

					}
				})
			},
			refresherrefresh() {
				var that = this
				that.page = 1

				that.refresherTriggered = true;
				console.log('自定义下拉刷新被触发');
				that.$refs.storeList0.requestData()
				that.$refs.storeList1.requestData()
				that.$refs.storeList2.requestData()
				that.$refs.storeList3.requestData()
				setTimeout(() => {

					that.refresherTriggered = false;
				}, 1000);
			},
			requestData() {
				var that = this

				that.http.sendRequest('Home.Home', {

				}).then(function(res) {
					if (res.code == 0) {
						const dic = res.info[0]
						that.banner = dic.banner
						that.search_keyword = dic.search_keyword
						that.array1 = dic.merchant_type[0]
						that.array2 = dic.merchant_type[1]
						// console.log(that.array3)
					}
				})
			},
			doShowAddress() {
				this.showTopAds = true
				// uni.hideTabBar() 
			},
			closeAdsView(e) {
				uni.showTabBar({
					success: res => {
						console.log(res)
					},
					fail: (error) => {
						console.log('fail', error)
					}
				})

				var that = this
				that.showBotAds = false
				that.showTopAds = false
				if (e != 0) {
					that.curAdsMsg = e
					// console.log(e)
					setTimeout(function() {
						uni.setStorage({
							key: 'userlocation',
							data: e,
							success() {
								console.log(2121212121)

								that.$refs.storeList0.requestData()
								that.$refs.storeList1.requestData()
								that.$refs.storeList2.requestData()
								that.$refs.storeList3.requestData()
							}

						})
					}, 500);
				}
			},
			doSearch(item) {
				uni.navigateTo({
					url: '/package-meishi/pages/search/index?ishome=1&word=' + item
				})
			},
			array1ItemClick(index,item) {
				if (item.id == 1) {
					var that = this
					that.http.sendRequest('Weather.Read', {
						'lng': that.longitude,
						'lat': that.latitude,
					}).then(function(res) {
						if (res.code == 0) {
							var a = res.info[0]
							if (a.is_good == 1) {
								uni.navigateTo({
									url: '/package-meishi/pages/index/index'
								})
							} else {
								uni.navigateTo({
									url: '/package-meishi/pages/tianqi/tianqi?t=' + a.des_title + '&d=' + a
										.des
								})
							}

						}
					})


				} else if (item.id == 2) {
					uni.navigateTo({
						url: '/package-shansong/pages/index/index'
					})
				} else if (item.id == 3) {
					uni.navigateTo({
						url: '../../package-other/pages/service/index/index'
					})
				} else if (item.id == 4) {
					uni.navigateTo({
						url: '../../package-other/pages/findShop/index/index'
					})
				}
			},
			array2ItemClick(index,item) {
				console.log(index)
				uni.navigateTo({
					url: '/package-other/pages/market/index/index?type=' + item.id
				})
			},
			tabbarClick(index) {
				this.tabIndex = index
			},
			animationfinish(e) {
				this.tabIndex = e.detail.current
			},
			gotoCell(item, type) {
				if (item.top_type_id == 1) {
					uni.navigateTo({
						url: '/package-meishi/pages/store/store?id=' + item.id
					})
				} else if (item.top_type_id == 3) {
					uni.navigateTo({
						url: '/package-other/pages/service/detaile/index?id=' + item.id
					})
				} else if (item.top_type_id == 4) {
					uni.navigateTo({
						url: '/package-other/pages/findShop/detail/index?id=' + item.id
					})
				} else {

					uni.navigateTo({
						url: '/package-other/pages/market/store/store?id=' + item.id
					})
				}

			},
			doMeishiClass(item) {
				uni.navigateTo({
					url: '/package-meishi/pages/class/class?id=' + item.id + '&name=' + item.use_name
				})
			},
			bannerTo(item) {
				if (item.url.length > 6) {
					uni.setStorage({
						key: 'weburl',
						data: item.url,
						success() {
							uni.navigateTo({
								url: '../webView/webView',
							});
						}
					})
				}
			}

		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #FFF;
	}

	.headerView {
		width: 100%;
		position: relative;
		background-color: #F6F6F6;
		position: relative;
	}

	.topImageView {
		width: 100%;
		overflow: hidden;
		height: 205px;
		background: linear-gradient(to right, #FF8D58, #FF5D37);
		/* background-color: #FF5725; */
		/* position: relative; */
		/* background-image: url("../../static/home/home-topbg.png"); */
		/* background-size: 100% 100%; */
	}



	.address_2 {
		margin-left: 5px;
		height: 22px;
		color: #FFF;
		font-size: 15px;
		font-weight: bold;
		line-height: 22px;
		width: 100%;
		align-items: center;
		margin-top: 52px;
	}

	.topSearchBtn {
		margin-top: 15px;
		margin-left: 15px;
		width: calc(100% - 30px);
		height: 40px;
		border-radius: 8px;
		background-color: #FFF;
		/* box-shadow: rgba(50,50,59,0.1) 10px 10px; */
		-moz-box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		-webkit-box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		align-items: center;
	}

	.wihteJianbian {
		width: 100%;
		background: linear-gradient(180deg, #FFF, #F6F6F6);
		display: flex;
		flex-direction: column;
		position: relative;
	}

	.sectionView {
		margin-left: 15px;
		width: calc(100% - 30px);
		/* background-color: #FFF; */
	}

	.fourthClassView {
		margin-top: 122px;
		width: 100%;
		display: flex;
		flex-direction: row;
	}

	.fourthClassItem {
		width: 14.933%;
	}

	.array2Item {
		width: calc(50% - 5px);
		margin-right: 10px;
		height: 56px;
		border-radius: 8px;
		align-items: center;
		margin-top: 10px;
		/* background-size: 100% 100%; */
		position: relative;
	}

	.array2Item-view {
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
		align-items: center;
		display: flex;
		flex-direction: row;
	}

	.array2Item:nth-child(2n) {
		margin-right: 0;
	}

	.array3Item {
		width: calc(25% - 8px);
		margin-right: 8px;
		height: 100px;
		border-radius: 8px;
		justify-content: center;
		align-items: center;
		margin-bottom: 10px;
		background-color: #F5F6F7;
		border-radius: 8px;
	}

	/* .array3Item:nth-child(4n) {
		margin-right: 0;
	} */
	.tab_sel_title {
		font-size: 16px;
		font-weight: bold;
		color: #FF6D22;
	}

	.tab_sel_des {
		margin-top: 3px;
		font-size: 12px;
		padding-left: 5px;
		padding-right: 5px;
		color: #FFF;
		height: 16px;
		border-radius: 8px;
		background: linear-gradient(to right, #FF8D58, #FF5D37);
		line-height: 16px;
	}

	.tab_nor_title {
		font-size: 16px;
		font-weight: bold;
		color: #323232;
	}

	.tab_nor_des {
		margin-top: 3px;
		font-size: 12px;
		/* padding-left: 5px;
		padding-right: 5px; */
